.login {
    height: 100vh;
    background-image: url(../../assets/assets/background_banner.jpg), linear-gradient(#0000007e, #0000007e);
    padding: 20px 8%;
}

.login-logo{
    width: 150px;
}
.login-form{
    width: 100%;
    max-width: 450px;
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 4px;
    padding: 60px;
    margin: auto;
}
.login-form h1{
  font-size:32px ;
  font-weight: 500;
  margin-bottom: 28px;

}
.login-form input{
    width: 100%;
    height: 50px;
    background-color: #333;
    color: white;
    margin: 12px 0;
    border: 0;
    outline: 0;
    border-radius: 4px;
    padding: 16px 20px;
    font-size: 16px;
    font-weight: 500;
}
.check {
    background-color: white;
    color: blue;
}
.login-form input::placeholder{
    font-size: 16px;
    font-weight: 500;
}
.login-form button{
    width: 100%;
    border: 0;
    outline: 0;
    padding: 16px;
    background: #e50914;
    color: white;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    margin-top: 20px;
    cursor: pointer;
}

.form-help{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #b3b3b3;
    font-size: 13px;

}
.remember{
    display: flex;
    align-items: center;
    gap: 5px;
}

.remember input{
    width: 18px;
    height: 18px;
    color: blue;
    background-color: white;
}

.form-switch {
    margin-top: 40px;
    color: #737373;
}
.form-switch span{
    margin-left: 6px;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
}