.footer-icons img{
    width: 30px;
    cursor: pointer;
    
}
.footer-icons {
    display: flex;
    gap: 20px;
    margin: 40px 0;
}

.footer ul {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 15px;
    list-style: none;
    margin-bottom: 30px;
}

.footer{
    padding: 30px 4%;
    max-width: 1000px;
    margin: 0 auto;
}

.copyright-text{
    color: gray;
    font-size: 14px;
    text-align: center;
    
   
}